export default "/* Loading.css */\r\n\r\n/* Styles for the Loading component */\r\n.loading {\r\n  display: flex;\r\n  align-items: flex-start;\r\n  justify-content: center;\r\n  flex-direction: column;\r\n  height: 80%;\r\n}\r\n.spinner-icon {\r\n  height: auto;\r\n  justify-content: space-between;\r\n}\r\n\r\n.loading-content {\r\n  justify-content: space-between;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n/* Loading.css */\r\n\r\n/* Styles for the Loading component */\r\n.loading-spinner {\r\n  display: flex;\r\n  font-size: 24px;\r\n  color: #1d1e1f;\r\n  align-items: center; /* Center vertically */\r\n  justify-content: space-between;\r\n}\r\n\r\n.spinner-icon {\r\n  animation: spin 1s linear infinite; /* Add a spin animation */\r\n}\r\n\r\n@keyframes spin {\r\n  0% {\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n.circle-container {\r\n  width: 24px;\r\n  height: 24px;\r\n  position: relative;\r\n}\r\n\r\n.half-circle {\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: #007bff;\r\n  border-radius: 50%;\r\n  clip-path: polygon(0% 0%, 100% 0%, 100% 50%, 0% 50%);\r\n  animation: spin 1s linear infinite; /* Clip the circle */\r\n}\r\n\r\n.loading-text {\r\n  margin-left: 10px; /* Adjust margin-left to create space between spinner and text */\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n}\r\n\r\n.loading-skeletons {\r\n  margin-top: 10px; /* Adjust margin-top to create space between content and skeletons */\r\n}\r\n\r\n.semi-circle {\r\n  width: 20rem;\r\n  height: 10rem;\r\n  background-color: #48abe0;\r\n  border-radius: 10rem 10rem 0 0;\r\n  transition: transform 0.5s ease-in-out;\r\n}\r\n\r\n/* Additional styles for the animated state */\r\n.animated {\r\n  animation: bounce 1s ease infinite alternate;\r\n}\r\n\r\n@keyframes bounce {\r\n  0% {\r\n    transform: translateY(0);\r\n  }\r\n  100% {\r\n    transform: translateY(-20px);\r\n  }\r\n}\r\n";